.home-banner-image-skeleton-container {
    height: 10em;
    width: 100%;
}
.home-skeleton-slide-container {
    position: relative;
    padding-top: 40%;
}
.home-skeleton-slide-element {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.home-skeleton-middle-container {
    position: relative;
    width: 49%;
    padding-top: 25%;
}
.home-skeleton-middle-element {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
@media screen and (max-width: 768px) {
    .home-banner-image-skeleton-container {
        height: 20vw;
    }
}