.home-container {
    width: 100%;
    animation: group-opacity 0.5s ease-out;
}

.home-toolbar {
    display: flex;
    flex-direction: row;
}

.home-header-wrapper {
    display: flex;
}

.home-navigation {
    min-width: 220px;
    align-self: flex-start;
}

.home-slider-nav {
    margin: auto;
    margin-top: 2em;
    background-color: transparent;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-right: none;
}

.home-slider-nav-title {
    /*border:1px solid rgba(0,0,0,0.1) ;*/
    background-color: #f7f8f9;
    padding: 0.5em;
    font-size: 1.2em;
    font-weight: bold;
}

.home-slider-body {
    /*border-top: 1px solid rgba(0, 0, 0, 0.1);*/
    /*padding-top: 1em;*/
    /*padding-bottom: 1em;*/
}

.home-slider-nav-element {
    /*width: 7em;*/
    width: 100%;
    height: 100%;
    /*margin-right: 2em;*/
    /*padding: 1em;*/
    /*padding-left: 1em;*/
    /*padding-right: 1em;*/
    box-sizing: border-box;
    border-right: 1px solid rgba(0, 0, 0, 0.1);

}


.home-slider-nav-wrapper {
    /*width: 90%;*/
    margin: auto;
    /*border: 1px solid rgba(0, 0, 0, 0.1);*/
    overflow: hidden;
    height: 100%;
    box-sizing: border-box;
    /*background-color: #f7f8f9;*/
    padding: 0.5em;
    border: 1px solid white;
}

.home-slider-nav-wrapper:hover {
    border: 1px solid var(--main-bg-color);
}

.home-slider-nav-name {
    font-size: 0.9em;
    text-align: center;
    color: black;
    /*font-weight: bold;*/
    margin-top: 0.5em;
    /*font-weight: 600;*/
}

.home-slider-nav-element:hover {
    cursor: pointer;
}

.home-slider-nav-element:hover .home-slider-nav-name {
    text-decoration: none;
}

.home-slider-nav-icon-container {
    width: 30%;
    position: relative;
    padding-top: 30%;
    margin: auto;
    /*background-color: red;*/
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border: none;
    transition: transform 0.1s ease-in;
}

.home-slider-nav-element:hover .home-slider-nav-icon-container {
    transform: scale(1.1);
    text-decoration: none;
}

.home-slider-inner-text {
    position: absolute;
    bottom: 0;
}


.home-slider-nav-icon {
    font-size: 2em;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    color: var(--main-bg-color);
}

.home-side {
    width: 20%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.home-side-wrapper {
    flex: 1;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: relative;
    overflow: hidden;
}
.home-side-wrapper:hover {
    cursor: pointer;
}

.side-1 {
    margin-bottom: 0.25em;
}

.side-2 {
    margin-top: 0.25em;
}

.home-side-ripple {
    position: absolute;
    height: 300%;
    width: 20%;
    transform: rotate(35deg);
    top: -150%;
    animation: moving 5s infinite;
    -webkit-animation: moving 5s infinite;
    background-color: rgba(255, 255, 255, 0.3);
}

.home-slide-container {
    padding-left: 0.5em;
    padding-right: 0.5em;
    width: 100%;
    /*height: 100%;*/
    box-sizing: border-box;
}

/*.home-product-sale {*/
/*    width: 100%;*/
/*    background-color: #f7f8f9;*/
/*    padding-top: 2em;*/
/*    padding-bottom: 2em;*/
/*}*/

.home-product-sale-container {
    border: 3px solid var(--third-bg-color);
    background-color: white;
    border-radius: 5px;
}

.home-product-sale-title {
    text-align: left;
    position: relative;
    box-sizing: border-box;
    background-color: var(--third-bg-color);
    display: flex;
}

.home-product-sale-title::before {
    content: '';
    position: absolute;
    height: 100%;
    width: 8em;
    background-image: url("../../assets/title_sale.png");
    background-size: cover;
}

.home-product-sale-text-title {
    display: inline-block;
    color: white;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 22px;
    position: relative;
    line-height: 1.7;
    z-index: 3;
    padding: 0.5em;
    opacity: 0.8;
    transition: color 0.1s ease-in;
}

.home-product-sale-text-title:hover {
    color: #ffb300;
    opacity: 1;
}

.home-product-sale-body {
    padding: 1em 0;
}

.home-product-sale-element {
    width: 100%;
    padding: 0.5em;
}

.home-banner-image-container {
    display: flex;
    justify-content: space-between;
}

.home-banner-image {
    width: 49%;
    height: 100%;
}

.home-product-sale-counter {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-right: 1em;
}
.home-nav-section {
    background-color: var(--section-bg-color);
}

.home-news-container {
    margin-top: 1em;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 2em;
}

@media screen and (max-width: 1280px) {

    .home-slider-nav {
        width: 100%;
    }
}

@media screen and (max-width: 768px) {
    .home-navigation {
        display: none;
    }
    .home-slide-container {
        padding: 0;
    }
    .home-toolbar {
        display: none
    }

    .home-slider-nav {
        width: 100%;
        margin-top: 0.5em;
    }

    .home-slider-nav-element {
        /*padding: 0 0.5em 0 0.5em;*/
    }

    .home-side {
        display: none;
    }

    .home-banner-image {
        width: 49%;
    }

    .third {
        display: none;
    }

    .home-product-sale-title {
        flex-direction: column;
        text-align: center;
    }

    .home-product-sale-counter {
        flex-direction: row;
        margin-right: 0;
        margin-bottom: 1em;
    }
}

@keyframes moving {
    0% {
        left: 0;
    }
    15% {
        left: 200%;
    }
    21% {
        left: 200%;
    }
    100% {
        left: 300%;
    }
}

@-webkit-keyframes moving {
    0% {
        left: 0;
    }
    15% {
        left: 200%;
    }
    21% {
        left: 200%;
    }
    100% {
        left: 300%;
    }
}