.home-category-banner {
    position: relative;
    width: 100%;
    padding-top: 28.57%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.home-category-inner-text {
    position: absolute;
    top: 0;
    z-index: -1;
    display: none;
}

.home-category-product-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
}

.home-category-element {
    width: 100%;
    box-sizing: border-box;
    padding: 0.5em;
}

.home-category-title {
    display: flex;
    flex-direction: row;
    /*border-bottom: 2px solid var(--main-bg-color);*/
    overflow: hidden;
    position: relative;
    border-top: 1px solid rgba(0,0,0,0.1);
    border-bottom: 1px solid rgba(0,0,0,0.1);
    box-sizing: border-box;
}

.home-category-child {
    padding: 10px;
    border-radius: 3px;
    margin-right: 1em;
    /*height: 100%;*/
    box-sizing: border-box;
    line-height: 2.2em;
    align-self: center;
    height: 100%;
}

.home-category-title-text {
    font-size: 1.3em;
    padding: 0.75em;
    color: white;
    box-sizing: border-box;
    position: relative;
}

.home-category-title-text::after {
    content: "";
    height: 100%;
    width: 30px;
    position: absolute;
    border-left: 28px solid var(--main-bg-color);
    border-top: 28px solid transparent;
    border-bottom: 28px solid transparent;
    border-right: 28px solid transparent;
    box-sizing: border-box;
    top: 0;
    right: -56px;;
}

.home-category-product-element {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    width: 100%;
    grid-template-rows: 1fr 1fr 0;
    /*padding: 1em;*/
    overflow: hidden;
}

.home-category-product-element-1 {
    box-sizing: border-box;
    width: 100%;
}

.home-category-banner-left-container {
    grid-row-start: 1;
    grid-row-end: 3;
    padding: 0.5em;
}

.home-category-banner-left {
    position: relative;
    min-width: 300px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    /*padding-top: 200%;*/
    height: 100%;
}

.home-category-text-inner {
    position: absolute;
    bottom: 0;
    right: 0;
}

.home-category-mobile {
    display: none;
}

.home-category-body-product-desktop {
    display: block;
    margin-top: 1em;
}

.home-category-banner-container {
    margin-right: 1em;
}

@media screen and (max-width: 1280px) {
    .home-category-product-element {
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }
}

@media screen and (max-width: 900px) {
    .home-category-banner-container {
        display: none;
    }
}

@media screen and (max-width: 768px) {
    .home-category-child {
        display: none;
    }

    .home-category-title-text {
        font-size: 1em;
        padding: 0.5em;
        color: white;
        box-sizing: border-box;
        position: relative;
    }

    .home-category-title-text::after {
        content: "";
        height: 100%;
        width: 20px;
        position: absolute;
        border-left: 18px solid var(--main-bg-color);
        border-top: 18px solid transparent;
        border-bottom: 18px solid transparent;
        border-right: 18px solid transparent;
        box-sizing: border-box;
        top: 0;
        right: -36px;;
    }

    .home-category-mobile {
        display: block;
        margin-top: 0.5em;
    }

    .home-category-product-container {
        display: none;
    }

    .home-category-body-product-desktop {
        display: none;
    }

    .home-category-element {
        padding-left: 0;
    }
}