.news-card-image {
    position: relative;
    padding-top: 66.66%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.news-card-inner-text {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: none;
}

.news-card-title {
    font-weight: normal;
    opacity: 0.7;
}

.news-card-title:hover {
    color: var(--third-bg-color);
    opacity: 1;
}

.news-card-more {
    border: none;
    padding: 0.5em;
    font-size: 0.9em;
    font-weight: bold;
    background-color: var(--main-bg-color);
    color: white;
}