.slide-container {
    width: 100%;
    position: relative;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently*/
    box-sizing: border-box;
    overflow-x: hidden;
    overflow-y: hidden;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
}

.slide-slides-images-container:hover .slide-slides-arrow-container {
    opacity: 0.8;
}

.slide-slides-images {
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    transition: 1s;
}

.slide-image {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 100%;
}

.slide-image-element {
    position: relative;
    background-size: cover;
    background-position: center;
    width: 100%;
    background-repeat: no-repeat;
}
.slide-image-element:hover {
    cursor: pointer;
}

.slide-text-inner {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    z-index: -1;
    display: none;
}


.slide-slides-arrow-container {
    position: absolute;
    top: 50%;
    height: 3em;
    transform: translateY(-50%);
    background-color: rgba(0, 0, 0, 0.3);
    opacity: 0;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    transition: opacity 0.2s ease-out;
}

.slide-container:hover .slide-slides-arrow-container {
    opacity: 1;
}

.left-arrow {
    left: 0;
    padding-right: 0.5em;
    padding-left: 0.5em;
}

.right-arrow {
    right: 0;
    padding-right: 0.5em;
    padding-left: 0.5em;
}

.left-arrow .fa-sort-up {
    color: #e0e0e0 !important;
    transform: rotate(-90deg);
}

.right-arrow .fa-sort-up {
    color: #e0e0e0 !important;
    transform: rotate(90deg);
}


@media screen and (max-width: 768px) {

    .slide-slides {
        width: 100%;
    }

}


